.project-body{
    background-color: rgba(255, 255, 255, 0.266);
    margin: 30px;
    padding: 30px;
    font-size: 1.2em;
}

.head-section{
    display: flex;
    justify-content: space-evenly;
    text-align: center;
}

.go-back{
    border: 2px solid #444;
    background-color: #eac4d5;
    text-decoration: none;
    padding: 5px 10px;
    border-radius: 10px;
    margin: auto 0;
}

.p-heading{
    text-align: center;
    width: 80%;
    margin: 0;
    padding: 5px;
    background-color: #d6eadf;
    color: #444;
    border: 2px solid #444;
    border-radius: 10px;
}

.p-links{
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-around;
    margin: 20px 0;
}

.p-link-btn{
    border: 2px solid #444;
    background-color: #b8e0d4;
    color: #444;
    text-decoration: none;
    margin: 0 10px;
    border-radius: 10px;
    padding: 10px;
}

.p-link-btn:hover, .go-back:hover{
    background-color: #809bce;
    box-shadow: 1px 1px #444;
}

.p-link-btn:active, .go-back:active{
    color: white;
}

.p-description{
    margin: 20px 0;
}

.p-description p{
    padding-left: 20px;
    /* text-align: center; */
    margin: 10px 0;
    line-height: 1.5em;
    
}

.p-description h2, .p-description h3{
    text-align: center;
    margin-bottom: 15px;
}

