.carousel-div {
    position: relative;
    width: 80%;
    overflow: hidden;
    margin: 0 auto;
    margin-top: 30px;
  }

.code-snippet{
    color: #2b2585;
    font-weight: bold;
}

.team-img{
    text-align: center;
}

.external-link{
    color: #85255d;
    /* text-decoration: none; */
}